.career-Banner-Container {
  width: 100%;
  height: auto;
}

.career-Banner-Container .career-Banner-Section {
  width: 100%;
  height: 90vh;
  display: flex;
  align-items: center;
  padding: 0 8%;
}

.career-Banner-Container .career-Banner-Section .section1 {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.career-Banner-Container .career-Banner-Section .section1 .heading h3 {
  font-size: 2rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  text-align: left;
  color: #464545;
}

.career-Banner-Container .career-Banner-Section .section1 .heading p {
  margin-top: 4%;
  font-size: 1rem;
  padding-right: 12%;
}

.career-Banner-Container .career-Banner-Section .section1 .heading button {
  margin-top: 4%;
  cursor: pointer;
  border: none;
  outline: none;
  color: white;
  padding: 8.8px 24.5px;
  border-radius: 5px;
  background-color: #3685c3;
  border: 2px solid #3685c3;
  transition: 0.3s ease-in;
}

.career-Banner-Container .career-Banner-Section .section1 .heading button:hover {
  background-color: white;
  color: #3685c3;
}

.career-Banner-Container .career-Banner-Section .section1 .heading button:focus {
  border: 2px solid #3685c3;
  outline: none;
}

.career-Banner-Container .career-Banner-Section .section2 {
  width: 55%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8%;
}

.career-Banner-Container .career-Banner-Section .section2 .img-box {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.career-Banner-Container .career-Banner-Section .section2 .img-box img {
  width: 100%;
  height: 100%;
}

.career-Banner-Container .career-Banner-Section .section3 {
  width: 5%;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.career-Banner-Container .career-Banner-Section .section3 span {
  width: 100%;
  padding: 0.4rem;
  margin-top: 1rem;
  text-align: center;
}

.career-Banner-Container .career-Banner-Section .section3 span a {
  color: #464545;
}

.career-Banner-Container .career-Banner-Section .section3 span a:hover i {
  color: #3685c3;
}

.career-Banner-Container .career-Banner-Section .section3 span a i {
  transition: 0.4s ease-out;
  font-size: 24px;
}

@media only screen and (max-width: 768px) {
  .career-Banner-Container .career-Banner-Section {
    height: auto;
    flex-direction: column;
  }
  .career-Banner-Container .career-Banner-Section .section1 {
    width: 100%;
    height: 100%;
  }
  .career-Banner-Container .career-Banner-Section .section2 {
    width: 100%;
    height: auto;
  }
  .career-Banner-Container .career-Banner-Section .section2 .img-box {
    width: 100%;
    height: auto;
  }
  .career-Banner-Container .career-Banner-Section .section2 .img-box img {
    width: 100%;
    height: 100%;
  }
  .career-Banner-Container .career-Banner-Section .section3 {
    width: 100%;
    height: auto;
    flex-direction: row;
  }
}

@media only screen and (min-width: 1441px) {
  .career-Banner-Container .career-Banner-Section {
    height: 90vh;
  }
  .career-Banner-Container .career-Banner-Section .section1 .heading h3 {
    font-size: calc(2rem + 2vh);
  }
  .career-Banner-Container .career-Banner-Section .section1 .heading p {
    font-size: calc(1rem + 2vh);
  }
  .career-Banner-Container .career-Banner-Section .section1 .heading button {
    font-size: calc(1rem + 2vh);
  }
  .career-Banner-Container .career-Banner-Section .section2 .img-box img {
    width: 100%;
    height: 100%;
  }
  .career-Banner-Container .career-Banner-Section .section3 {
    width: 5%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .career-Banner-Container .career-Banner-Section .section3 span {
    width: 100%;
    padding: 0.4rem;
    margin-top: 4rem;
    text-align: center;
  }
  .career-Banner-Container .career-Banner-Section .section3 span a {
    color: #464545;
  }
  .career-Banner-Container .career-Banner-Section .section3 span a:hover i {
    color: #3685c3;
  }
  .career-Banner-Container .career-Banner-Section .section3 span a i {
    transition: 0.4s ease-out;
    font-size: 2.5rem;
  }
}
